<template>
    <div class="loan">
        <v-header :type=2>惠鑫贷</v-header>
        <scroll ref='loanList' :data="loanArr" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType" :pullup="isShow"  :pulldown="isShow" @pulldown="pulldown"  @scrollToEnd="pullup" class="loan-list" >
            <ul class="loan-list-content">
                <li class="loan-item" v-for="(item,index) in loanArr" :key="index">
                    <h3>{{item.financeName}}</h3>
                    <div class="standard">
                        <a @click.stop="showStandardDesc(index,$event)">申请提交资料及二手车准入标准<i class='icon-cycle-arrow'></i></a>
                        <p v-show="itemIndex == index">{{item.standard}}</p>
                    </div>
                    <div class="loan-card">
                        <div class="loan-icon">
                            <!-- <div class="title">
                                {{item.financeName}}
                            </div> -->
                            <div class="icon">
                                <img :src="item.icon" alt="">
                            </div>
                        </div>
                        <div class="loan-desc">
                            <table>
                                <tr>
                                    <td>最低首付比例</td>
                                    <td>最低贷款额</td>
                                </tr>
                                <tr>
                                    <td><span>{{item.rate}}</span></td>
                                    <td><span>{{item.moneys}}</span></td>
                                </tr>
                                <tr>
                                    <td>贷款期限</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td><span>{{item.rate}}</span></td>
                                    <td><button class="weui-btn" @click="selectLoan(item.id)">立即申请</button></td>
                                </tr>
                            </table>
                        </div>
                    </div>
                </li>
            </ul>
        </scroll>
        <router-view></router-view>
    </div>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import Scroll from '@/components/base/scroll/scroll'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data(){
        return{
           scrollY: 0,
           isShow: true,  
           page:1,
           itemIndex:null,
           loanArr:[]
        }
    },
    created() {
      this.probeType = 3
      this.listenScroll = true
    },

    methods:{
          pulldown(){  
            this.carArr = [];
            this.page = 1;
            //下拉刷新重新初始化             
            this.$refs.loanList.$emit('pullrefresh.finishLoad');  
            this.getLoanList();
        },  
        pullup(){  
            if(!this.page) return  
            this.page ++   
            //上拉加载重新初始化  
            this.$refs.loanList.$emit('infinitescroll.reInit');
            this.getLoanList(this.page);  
        },  
        showStandardDesc(index,e){
            console.log(index)
            if(this.itemIndex == index){
                this.itemIndex = null;
            }else{
                this.itemIndex = index;
            }           
        },
        selectLoan(id){
            this.$emit("selectLoan",id);
        },
        getLoanList(page){
            let _this = this;
            let data = new Object();
            data.platForm = '020005';
            data.pageSize = '10';
            if(page){
                data.pageNum = page;
            }else{
                data.pageNum = 1;
            }
            $.ajax({
                url:MOBILE_COMMONURL+"/queryFinanceList",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        if(data.data.financeList.length > 0){  
                            _this.loanArr = [..._this.loanArr, ...data.data.financeList]  
                            //单次请求数据加载完毕后  
                            _this.$refs.loanList.$emit('infinitescroll.finishLoad');  
                        }else{  
                            //把page设置成false 数据已经加载完毕了 不用在加载了  
                            _this.page = false  
                            //所有数据加载完毕后  
                            _this.$refs.loanList.$emit('infinitescroll.loadedDone');  
                        }
                    }
                }
            })
        },
         scroll(pos) {
            this.scrollY = pos.y;
            console.log(pos.y);
        }, 
    },
    mounted(){
        this.getLoanList();
        this.$refs.loanList.$el.style.top = `44px`;  
    },
    components:{
        vHeader,
        Scroll
    }
}
</script>



