<template>
    <transition name="slide">
        <v-loan @selectLoan="selectLoan"></v-loan>
    </transition>
</template>

<script>
import vLoan from '@/components/mobile/m-loan'
export default {
   components:{
       vLoan
   },
   methods:{
       selectLoan(data){
           this.$router.push({
               path:"/home/loanList/loanform"               
           })
       }
   }
}
</script>



